import * as React from 'react';
import { SignInForm } from './SignInForm';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { push } from 'react-router-redux';
import { signInAction as signIn, saveTokens } from '@store/modules/registration';
import { userLogIn, authenticate } from '@store/modules/user';
import { hidePopUp } from '@store/modules/common';
import { LocalStorage } from '@common/Utils';
import PropTypes from 'prop-types';

const ErrorMessage = 'Incorrect email or password';

class SignInFormContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
      checked: true,
      errorMessage: '',
      isError: false,
      preloader: false
    };
  }

  static propTypes = {
    push: PropTypes.func,
    signIn: PropTypes.func,
  };

  handleSubmit = event => {
    event.preventDefault();

    this.props.hidePopUp();
    this.preloaderFunc();

    const data = {
      email: this.state.email,
      password: this.state.password
    };
    const { signIn } = this.props;
    const { onErrorSubmit, onSuccessSubmit } = this;

    signIn(data)
      .then((res) => {
        const { payload: { data } } = res;

        if (data && data.error) {
          onErrorSubmit();
        } else if (data) {
          onSuccessSubmit(data);
        }

        console.log(res, data);
        //this.onSuccessSubmit.bind(this)
      })
      .catch(onErrorSubmit);
  };

  onSuccessSubmit = (res) => {
    this.props.saveTokens(res);

    //this.props.userLogIn();
    this.props.push('/profile');
  }

  preloaderFunc = event => {
    this.setState({preloader: true});
  }

  onErrorSubmit = (err) => {
    this.setState({
      preloader: false,
      errorMessage: ErrorMessage,
      isError: true
    });
  }

  handleEnter = event => {
    this.setState({[event.target.name]: event.target.value});
  };

  onClickDemo = (event) => {
    event.preventDefault();
    this.props.signIn({
      email: 'guest@mail.com',
      password: '12345678'
    }).then(this.onSuccessSubmit.bind(this))
    .catch(this.onErrorSubmit.bind(this));
  }

  render() {
    const {
      handleSubmit,
      handleEnter,
      preloaderFunc,
      state: {
        email,
        password,
      }
    } = this;
    return <SignInForm
      handleSubmit={handleSubmit.bind(this)}
      preloaderFunc={preloaderFunc}
      preloader={this.state.preloader}
      errorMessage={this.state.errorMessage}
      handleEnter={handleEnter.bind(this)}
      checked={this.state.checked}
      isError={this.state.isError}
      onClickDemo={this.onClickDemo}
      inputsValue={
        {          
          email,
          password,
        }
      }
    />;
  }
}

const mapDispatchToProps = dispatch => {
  return { 
    ...bindActionCreators({
      push,
      signIn,
      userLogIn,
      hidePopUp,
      authenticate
    }, dispatch),

    saveTokens
  }
}

const connectedContainer =
  connect(null, mapDispatchToProps)(SignInFormContainer);

export {connectedContainer as SignInFormContainer};



