import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { TraderCard } from './TraderCard';
import { showPopUp } from '@store/modules/common';
import { getProductById } from '@store/modules/marketplace';

class TraderCardContainer extends React.Component {
  constructor(props) {
    super(props);  
  }

  render() {
    return (
      <TraderCard
        onClick={this.onClickRoot}
        {...this.props}
      />
    );
  }

  onClickRoot = () => {
    const { showPopUp, getProductById, productId, type } = this.props;
    const filterBalancesForZero = (balancesArr) => balancesArr.filter((cur, key) => cur.available > 0);

    console.log(type);

    getProductById({
      productId,
      type
    })
      .then((res) => {
        const { payload: { data } } = res;

        console.log(res);

        const propData = {
          ...data,
          balances: data.balances ? filterBalancesForZero(data.balances) : []
        };

        showPopUp('productPage', {
          ...propData
        });
      })   
  };
}

const mapStateToProps = state => {
  return {
    history: state.marketplace.historyData
  };
}

const mapDispatchToProps = dispatch =>
  bindActionCreators({showPopUp, getProductById}, dispatch);

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(TraderCardContainer);

export {connectedContainer as TraderCardContainer};



