import * as React from 'react';
import { PopUpNewProduct } from './PopUpNewProduct';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { addProduct } from '@store/modules/user';
import { hidePopUp, socketSubscribe } from '@store/modules/common';
import { getKeys, getMyProducts } from '@store/modules/user';

class PopUpNewProductContainer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      formData: {
        keyId: '', 
        name: '', 
        baseAsset: '', 
        typeId: '',
        description: '', 
      },
      error: {
        isError: false,
        errorMessage: ''
      }
    };
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const data = this.state.formData;    

    if (this.checkData(data)) {
      this.sendData(data);
    }
  };

  checkData(data) {
    const { formData } = this.state;

    let resultCheck = {
      isError: false,
      errorMessage: ''
    };

    for (let key in formData) {
      if (formData[key].length < 1) {
        resultCheck = {
          isError: true,
          errorMessage: `${key.toUpperCase()} length from 2 to 50. Fill in all the field.`
        }      
        break;
      }
    }

    if (resultCheck.isError === false) return true;

    this.setState({
      ...this.state,
      error: resultCheck
    });

    return false;
  }

  sendData(data) {
    const { addProduct, hidePopUp, getMyProducts } = this.props;

    addProduct(data).then((res) => {
      console.log(res.payload);
      if (res.payload.statusText == "OK") {
        hidePopUp();
        getMyProducts();

        setTimeout(getMyProducts,2000);
      }
    });
  }

  changeState = (key, value) => {
    this.setState({ 
      ...this.state,
      formData: {
        ...this.state.formData,
        [key]: value
      }
    });
  }

  render() {
    const {
      handleSubmit,
      changeState
    } = this;

    return <PopUpNewProduct
      handleSubmit={handleSubmit}
      changeState={changeState}
      isError={this.state.error.isError}
      errorMessage={this.state.error.errorMessage}
      api_keys={this.props.api_keys}
    />;
  }
}

const mapStateToProps = state => {
  return {
    role: state.user.personalInfo.role,
    api_keys: state.user.burses
  };
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {addProduct, hidePopUp, getKeys, socketSubscribe, getMyProducts}, dispatch);

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(PopUpNewProductContainer);

export {connectedContainer as PopUpNewProductContainer};



