import * as React from 'react';
import { AddKeys } from './AddKeys';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { showPopUp } from '@store/modules/common';

class AddKeysContainer extends React.Component {

  onClick = () => {
    this.props.showPopUp('addApiKeys');
  }

  render() {
    return (<AddKeys onClick={this.onClick} />);
  }
}

const mapDispatchToProps = dispatch =>
  bindActionCreators({showPopUp}, dispatch);

const connectedContainer =
  connect(null, mapDispatchToProps)(AddKeysContainer);
export { connectedContainer as AddKeysContainer };



