import { Utils } from '@common/Utils';

import { staticState } from '@store/static';

export const GET_PRODUCTS = 'marketplace/GET_PRODUCTS';
export const GET_PRODUCTS_SUCCESS = 'marketplace/GET_PRODUCTS_SUCCESS';
export const GET_HISTORY = 'marketplace/GET_HISTORY';
export const GET_HISTORY_SUCCESS = 'marketplace/GET_HISTORY_SUCCESS';
export const GET_PRODUCT_BY_ID = 'marketplace/GET_PRODUCT_BY_ID';

const initialState = {
  products: [],
  historyData: {},
}

export const marketplace = (state = initialState, action) => {
  switch (action.type) {
    case GET_PRODUCTS_SUCCESS:
      return {
        ...state,
        products: staticState.concat(action.payload.data),
      };    
    // case GET_HISTORY_SUCCESS:
    //   const history = Utils.parseTradeHistory(action.payload.data.graph[0]);
    //   return {
    //     ...state,
    //     historyData: {
    //       ...state.historyData,
    //       [action.payload.data.productId]: history
    //     }
    //   };
    default:
      return state;
  }
}

export const getProducts = () => {
  return {
    type: GET_PRODUCTS,
    payload: {
      request: {
        method: 'GET',
        url: '/products'
      }
    }
  };
}

export const getProductById = (data) => {
  const { productId, type } = data;

  return {
    type: GET_PRODUCT_BY_ID,
    payload: {
      request: {
        method: 'GET',
        url: type === "profile" ? `/user/products/${productId}` : `/products/${productId}`
      }
    }
  };
}



