import * as React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { getProductById } from '@store/modules/marketplace';
import { showPopUp } from '@store/modules/common';


const ROOT_CLASS = 'api';

const status = {
  false: 'Invalid',
  true: 'Valid'
}

class Api extends React.Component {
  onClickRoot = (productId) => {
    const { showPopUp, getProductById } = this.props;
    const filterBalancesForZero = (balancesArr) => balancesArr.filter((cur, key) => cur.available > 0);

    getProductById({
      productId,
      type: 'marketplace'
    })
      .then((res) => {
        console.log(404, res);
        const { payload: { data } } = res;
        const propData = {
          ...data,
          balances: data.balances ? filterBalancesForZero(data.balances) : []
        };

        showPopUp('productPage', {
          ...propData
        });
      })   
  };

  render() {
    const { user: { myFollowings, }, } = this.props;

    return (
      <div className={ROOT_CLASS}>
        <div className={`${ROOT_CLASS}__head`}>
          <div className={`${ROOT_CLASS}__exchange ${ROOT_CLASS}__exchange_${this.props.stock}`}></div>
          <div className={`${ROOT_CLASS}__status ${ROOT_CLASS}__status_${this.props.valid?'valid':'error'}`}>
            { status[this.props.valid] }
          </div>
        </div>
        {
          myFollowings
            .filter(cur => cur.selfKeyId == this.props.keyId)
            .map((cur, key) => (
              <div key={key} className={`${ROOT_CLASS}__followsBlock`}>Now folows for 
                <span onClick={()=>this.onClickRoot(cur.productId)}> Product({cur.productId}) </span>
              </div>
            ))
        }
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    user: state.user
  };
};

const mapDispatchToProps = dispatch =>
  bindActionCreators({showPopUp, getProductById}, dispatch);

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(Api);

export {connectedContainer as Api};



