import * as React from 'react';
import { PopUpAddApiKeys } from './PopUpAddApiKeys';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { addExchange, addKeys } from '@store/modules/user';
import { hidePopUp, socketSubscribe } from '@store/modules/common';
import { getKeys, getMyProducts } from '@store/modules/user';

class PopUpAddApiKeysContainer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      formData: {
        stock: '', 
        apiKey: '', 
        apiSecret: '',
      },
      error: {
        isError: false,
        errorMessage: ''
      }
    };
  }

  handleSubmit = (event) => {
    event.preventDefault();
    const data = this.state.formData;    

    if (this.checkData(data)) {
      this.sendData(data);
    }
  };

  checkData(data) {
    const { formData } = this.state;

    let resultCheck = {
      isError: false,
      errorMessage: ''
    };

    for (let key in formData) {
      if (formData[key].length < 1) {
        resultCheck = {
          isError: true,
          errorMessage: `${key.toUpperCase()} length from 2 to 50. Fill in all the field.`
        }      
        break;
      }
    }

    if (resultCheck.isError === false) return true;

    this.setState({
      ...this.state,
      error: resultCheck
    });

    return false;
  }

  async sendData(data) {
    const { addKeys, getKeys, hidePopUp } = this.props;
    
    addKeys(data).then((res) => {
      console.log(res.payload);
      if (res.payload.statusText == "OK") {
        hidePopUp();
        getKeys();

        setTimeout(getKeys,2000);
      }
    });
  }

  changeState = (key, value) => {
    this.setState({ 
      ...this.state,
      formData: {
        ...this.state.formData,
        [key]: value
      }
    });
  }

  render() {
    const {
      handleSubmit,
      changeState
    } = this;

    return <PopUpAddApiKeys
      handleSubmit={handleSubmit}
      changeState={changeState}
      isError={this.state.error.isError}
      errorMessage={this.state.error.errorMessage}
    />;
  }
}

const mapStateToProps = state => {
  return {role: state.user.personalInfo.role};
}

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {addExchange, hidePopUp, getKeys, addKeys, getMyProducts}, dispatch);

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(PopUpAddApiKeysContainer);

export {connectedContainer as PopUpAddApiKeysContainer};



