import * as React from 'react';
import { PopUpProductPage } from './PopUpProductPage';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import {
  subscribeOnTrader,
  unsubscribeTrader,
  getSubscribedProducts
} from '@store/modules/user';
import { hidePopUp } from '@store/modules/common';
import { getProducts } from '@store/modules/marketplace';
import {
  getKeys,
  getFollowings,
} from '@store/modules/user';

class PopUpProductPageContainer extends React.Component {
  componentDidMount() {
    const { getKeys, getFollowings, } = this.props;

    getFollowings();
    getKeys();
  }

  render() {
    const { userStocks, myFollowings } = this.props;

    return (
      <PopUpProductPage
        {...this.props}
        onClickConnect={this.onClickConnect}
        onClickDisconnect={this.onClickDisconnect}
        isNeedHide={this.props.products.indexOf(this.props.id) >= 0}
        isNeedHideDisc={this.props.products.indexOf(this.props.id) < 0}
        userStocks={userStocks}
        myFollowings={myFollowings}
      />
    );
  }

  onClickConnect = (productId, keyId, stock) => {
    const { subscribeOnTrader, getKeys, getFollowings, } = this.props;
    console.log("onClickConnect", keyId, stock);

    subscribeOnTrader({keyId, stock, productId}).then(()=>{
      getFollowings();
      getKeys();
    });
  }

  onClickDisconnect = (productId, keyId, stock) => {
    const { unsubscribeTrader, getKeys, getFollowings, } = this.props;
    console.log("onClickConnect", keyId, stock);

    unsubscribeTrader({keyId, stock, productId}).then(()=>{
      getFollowings();
      getKeys();
    });
  }

  onFinishConnect = () => {
    this.props.getSubscribedProducts();
    this.props.getProducts();
    this.props.hidePopUp();
  }
}

const mapStateToProps = state => {
  return {
    userStocks: state.user.burses,
    myFollowings: state.user.myFollowings,
    personalInfo: state.user.personalInfo,
    products: state.user.products,
    productsManager: state.marketplace.products,
  };
};

const mapDispatchToProps = dispatch =>
  bindActionCreators({
    unsubscribeTrader,
    subscribeOnTrader,
    hidePopUp,
    getSubscribedProducts,
    getProducts,
    getKeys,
    getFollowings,
  }, dispatch);

const connectedContainer =
  connect(mapStateToProps, mapDispatchToProps)(PopUpProductPageContainer);

export { connectedContainer as PopUpProductPageContainer };



