import React from 'react';
import { Button } from '@components/common/Button';
import { Select } from '@registration/select';
import { Input } from '@common/Input';
import { TextArea } from '@common/TextArea';
import { objectLangs, lng } from '../../../../lngs/index';

const ROOT_CLASS = 'pop-up-new-product';

export const PopUpNewProduct = (props) => {
  const { changeState } = props;

  const listKeys = () => {
    return props.api_keys.reduce((pr, el, key, arr) => {
      if (!el.keyId || !el.stock || !el.valid) return pr;
      return pr.concat({
        value: el.keyId, 
        label: `${el.stock.toUpperCase()}, keyId ${el.keyId}`
      })
    }, [])
  };

  const midlChangeValues = (key) => {
    return (e) => changeState(key, e.payload ? e.payload.value : e.target.value);
  };

  return (
    <form className={ROOT_CLASS} onSubmit={props.handleSubmit}>
      <div className={`${ROOT_CLASS}__title`}>{ objectLangs[lng]['PopUpNewProduct#1'] }</div>
      <div className={`${ROOT_CLASS}__error ${!props.isError ? ROOT_CLASS + '__error_hidden' : ''}`}>
        {props.errorMessage}
      </div>
      <div className={`${ROOT_CLASS}__select-wrap`}>
        <Select
          onChange={midlChangeValues('keyId')}
          defaultOption={{
            value: 'none',
            label: 'Select your valid keys pair'
          }}          
          options={listKeys()}
          theme="sign-up"
        />
      </div>
      <div className={`${ROOT_CLASS}__input-wrap ${ROOT_CLASS}__input-wrap_name`}>
        <Input
          onChange={midlChangeValues('name')}
          placeholder={ objectLangs[lng]['PopUpNewProduct#3'] }
          type="text"
          required
        />
      </div>
      <div className={`${ROOT_CLASS}__select-wrap`}>
        <Select
          onChange={midlChangeValues('baseAsset')}
          defaultOption={{
            value: 'none',
            label: 'Select base asset'
          }}
          options={[
            // {
            //   value: 'BTC',
            //   label: 'BTC'
            // },
            {
              value: 'USDT',
              label: 'USD'
            }
          ]}
          theme="sign-up"
        />
      </div>
      <div className={`${ROOT_CLASS}__select-wrap`}>
        <Select
          onChange={midlChangeValues('typeId')}
          defaultOption={{
            value: 'none',
            label: 'Select type permision'
          }}
          options={[
            {
              value: 0,
              label: 'Public'
            },
            {
              value: 1,
              label: 'Private'
            }
          ]}
          theme="sign-up"
        />
      </div>
      <div className={`${ROOT_CLASS}__textarea`}>
        <TextArea
          onChange={midlChangeValues('description')}
          areaProps={{
            placeholder: objectLangs[lng]['PopUpNewProduct#6']
          }}
        />
      </div>
      <div className={`${ROOT_CLASS}__footer`}>
        <div className={`${ROOT_CLASS}__button-wrap`}>
          <Button
            className={`${ROOT_CLASS}__button`}
            theme="gradient-img"
            NameBtn={ objectLangs[lng]['PopUpNewProduct#7'] }
          />
        </div>
      </div>
    </form>
  );
};



