import * as React from 'react';
import { Avatar } from '@profile/Avatar';
import DefaultIcon from '@assets/images/profile-icon.svg';
import BinanceLogo from '@assets/images/BINANCE_LOGO.png';
import HitbtcLogo from '@assets/images/hitbtc_icon_white.png';
import { Rating } from '@marketplace/Rating';
import avatarArr from './importAvatars';

const ROOT_CLASS = 'user';

export const User = (props) => {
  const {
    rating,
    block,
  } = props;

  return (
    <div className={`${ROOT_CLASS} user__${props.block}`}>
      <div className={`${ROOT_CLASS}__persInfo`}>
        <div className={`${ROOT_CLASS}__avatar-wrap`}>
          <Avatar productName={props.nameProduct} theme={props.theme}/>
        </div>
        <div className={`${ROOT_CLASS}__info`}>
          <div className={`${ROOT_CLASS}__name-wrap`}>
            {props.block === "profileHead" ? `${props.firstName} ${props.lastName}` : props.nameProduct}
          </div>
          {
            (props.block === "productPage" || props.block === "traderCard") && (
              <div className={`${ROOT_CLASS}__exchangeLogo`}>
                <img src={props.stock === "binance" ? BinanceLogo : HitbtcLogo} />
              </div>
            )
          }        
        </div>
      </div>
      <div className={`${ROOT_CLASS}__rating`}>
        {
          rating && Object.keys(rating).length > 0 && !isNaN(rating.value) ?
            <Rating
              rating={rating}
              block={block}
            />
          :
            rating !== false && <div className={`${ROOT_CLASS}__norating`}>Rating: N/A</div>
        }
      </div>
    </div>
  );
};




