import React from 'react';
import { Utils } from '@common/Utils';

import Highcharts from 'react-highcharts'; 

const ROOT_CLASS = 'rating';

const blocksInfo = {
  'productPage': {
    widthChart: 200,
    heightChart: 120,
  },
  'traderCard': {
    widthChart: 200,
    heightChart: 120,
  },
};

export const Rating = props => {
  const { block, rating } = props;

  const {
    allTimeHigh,
    incomeAverage, 
    maxDrawdown, 
    maxRecoveryTimeShare, 
    value,
  } = rating;

  let score = value > 0.0999 ? (value * 100).toFixed(0) : (value * 100).toFixed(2);
  score = score > 100 ? 100 : score;
  score = score < -100 ? -100 : score;

  let _block = blocksInfo[block];

  return (
    <div className={`${ROOT_CLASS} rating_${props.block}`}>
      {<Highcharts
        config={{
          chart: {
            height: _block && _block.heightChart,
            width:  _block && _block.widthChart,
            type: 'container',

            plotBackgroundColor: null,
            plotBorderWidth: 0,
            plotShadow: false,
            backgroundColor: 'rgba(0,0,0,0)',
          },  

          credits: {
            enabled: false
          },

          title: {
            text: `${score}`,
            align: 'center',
            verticalAlign: 'middle',
            y: 60,
            x: 60,

            style: {
              color: '#A68EED',
              fontWeight: 'bold',
              fontSize: 12,
            }
          },

          tooltip: {
            pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
          },

          plotOptions: {
            pie: {
              colors: [`${score>=0?'green':'red'}`, '#ccc'],
              dataLabels: {
                enabled: true,
                distance: -50,
                style: {
                  fontWeight: 'bold',
                  color: 'white'
                }
              },
              startAngle: -90,
              endAngle: 90,
              center: ['94%', '162%'],
              size: '100%'
            }
          },
          series: [{
            type: 'pie',
            name: 'Rating score',
            innerSize: '80%',
            data: [
              ['', Math.abs(score) < 1 ? 1 : Math.abs(score) ],
              {
                name: '',
                y: 5 - Math.abs(score),
                dataLabels: {
                  enabled: false
                }
              }
            ]
          }]
        }}
      />}
      <div className={`${ROOT_CLASS}__title`}>Rating score</div>
    </div>
  );
};



