import * as React from 'react';
import { TraderCard } from '@marketplace/TraderCard';
import { AddProduct } from '@profile/AddProduct';
import { objectLangs, lng } from '../../../lngs/index'

const ROOT_CLASS = 'products';

export const Products = (props) => {
  const renderList = () => {
    return props.products && props.products.length > 0 ? 
      (
        props.products.map((product, index) => {
          return (            
            <TraderCard
              {...product}
              key={index}
              type="profile"
            />
          );
        })
      ) : (<div className={`${ROOT_CLASS}__placeholder`}>{ objectLangs[lng]['Products#2'] }</div>);
  }

  return (
    <div className={ROOT_CLASS}>
      <div className={`${ROOT_CLASS}__header`}>
        <div className={`${ROOT_CLASS}__title`}>{ objectLangs[lng]['Products#1'] }</div>
        <div className={`${ROOT_CLASS}__add-product`}></div>
        <AddProduct />
      </div>
      <div className={`${ROOT_CLASS}__list`}>
        { renderList() }      
      </div>
    </div>
  );
}



