import * as React from 'react';
import { Utils } from '@common/Utils';
import { User } from '@common/User';
import { Unit } from '@common/Unit';

import Highcharts from 'react-highcharts'; 
import HighchartsMore from 'highcharts-more';

import { objectLangs, lng } from '../../../../lngs/index';

HighchartsMore(Highcharts.Highcharts)

const ROOT_CLASS = 'product-page';

export class PopUpProductPage extends React.Component {
  render() {
    const { props } = this;
    const { userStocks, myFollowings } = props;

    let {
      rating,
      trader,
      balances,
      name,
      baseAsset,
      description,
      productId,
    } = props;

    let {
      allTimeHigh,
      baseBalanceHistory,
      incomeAverage,
      maxDrawdown,
      maxRecoveryTimeShare,
      value,
    } = rating;

    let polarItems;

    if (Object.keys(rating).length > 0) {
      polarItems = {
        // "Volatility": ((Math.random() * (1 - -1 + 1)) + -1),
        "Volatility": maxDrawdown - (maxDrawdown/100*23),
        "IncomeAverage": incomeAverage,
        "MaxDrawdown": maxDrawdown,
        "DmaxDD": maxRecoveryTimeShare,
        "Sharp ratio": incomeAverage / ((Math.random() * (1 - -1 + 1)) + -1),
      };

      polarItems = Object.keys(polarItems).reduce((pr, cur, index) => {
        pr[cur] = polarItems[cur] * 10;
        return pr;
      }, {});

      polarItems["Sharp ratio"] = polarItems["Sharp ratio"] < 0 ? -polarItems["Sharp ratio"] : polarItems["Sharp ratio"] 

    } else {
      polarItems = {
        allTimeHigh,
        incomeAverage,
        maxDrawdown,
        maxRecoveryTimeShare,
        value,
      };
    }
 
    baseBalanceHistory = baseBalanceHistory
      .filter((cur, index) => {
        // console.log(221, (index < 200))
        return (cur.value !== 0); //&& (index > 500);
      });

    return (
      <div className={ROOT_CLASS}>
        <div className={`${ROOT_CLASS}__user-block`}>
          <User
            nameProduct={name}
            theme="small"
            rating={rating}
            block="productPage"
            stock={productId === "5c663474ab804600195d8cac" ? "hitbtc" : "binance"}
          />
        </div>
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Trader:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{trader.firstName} {trader.lastName}</div>
        </div>  
        {
          rating && Object.keys(rating).length > 0 && !isNaN(rating.value) ?
            <React.Fragment>
              <div className={`${ROOT_CLASS}__infoblock`}>
                <div className={`${ROOT_CLASS}__infoblocktitle`}>Rating metrics:</div>
              </div>
              <div className={`${ROOT_CLASS}__ratingvars`}>
                <div className={`${ROOT_CLASS}__ratingvars__left`}>  
                  {<Highcharts 
                    config={{
                      chart: {
                        polar: true,
                        type: 'line',
                        backgroundColor: 'rgba(0,0,0,0)',

                        height: 200
                      },
                      title: {
                        text: null
                      },
                      pane: {
                        size: '80%'
                      },
                      xAxis: {
                        categories: Object.keys(polarItems),
                        tickmarkPlacement: 'on',
                        lineWidth: 0
                      },
                      yAxis: {
                        gridLineInterpolation: 'polygon',
                        lineWidth: 0,
                      },
                      tooltip: {
                        shared: false,
                        // pointFormat: '<span style="color:{series.color}"><b>{point.y:,.0f}</b><br/>'
                      },
                      tooltip: { enabled: false },
                      legend: {
                        enabled: false
                      },   
                      credits: {
                        enabled: false
                      },           
                      series: [{
                        data: [
                          ((maxDrawdown+1)+(maxDrawdown/100*23)),
                          ((maxDrawdown+1)+(maxDrawdown/100*25)),
                          ((maxDrawdown+1)+(maxDrawdown/100*28)),
                          ((maxDrawdown+1)+(maxDrawdown/100*33)),
                          ((maxDrawdown+1)+(maxDrawdown/100*37)),
                        ], //Object.keys(polarItems).map(cur => polarItems[cur]),
                        pointPlacement: 'on',
                      }]  
                    }}
                  />}
                </div>
                <div className={`${ROOT_CLASS}__ratingvars__right`}>
                  {
                    Object.entries(polarItems).map((cur, key) =>  
                      <div className={`${ROOT_CLASS}__ratingvars__right_item`} key={key}>
                        <span>{cur[0]}</span> : <span>{cur[1]}</span>
                      </div>
                    ) 
                  }
                </div>
              </div>
            </React.Fragment>
          :
            <div className={`${ROOT_CLASS}__infoblock`}>
              <div className={`${ROOT_CLASS}__infoblocktitle`}>Rating:</div>
              <div className={`${ROOT_CLASS}__infoblockcontent`}>N/A</div>
            </div>  
        }
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Base asset:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{baseAsset}</div>
        </div>
        {
          (typeof baseBalanceHistory === "object") ?
            <div className={`${ROOT_CLASS}__incomechart`}>
              <div className={`${ROOT_CLASS}__infoblock`}>
                <div className={`${ROOT_CLASS}__infoblocktitle`}>Income:</div>
              </div>  
              <Highcharts
                config={{
                  chart: {
                    // width: (baseBalanceHistory.length) * ((baseBalanceHistory.length) < 50 ? 35 : 15),
                    type: 'area',
                    backgroundColor: 'rgba(0,0,0,0)',
                    zoomType: 'x',
                    // width: 2000,
                  },
                  title: {
                    text: 'USD to EUR exchange rate over time'
                  },
                  subtitle: {
                    text: document.ontouchstart === undefined ?
                        'Click and drag in the plot area to zoom in' : 'Pinch the chart to zoom in'
                  },
                  xAxis: {
                    type: 'datetime',
                    allowDecimals: false,
                  },
                  yAxis: {
                    title: {
                      text: 'Exchange rate'
                    },
                  },
                  legend: {
                    enabled: false
                  },
                  plotOptions: {
                    area: {
                      marker: {
                        radius: 2
                      },
                      lineWidth: 1,
                      states: {
                        hover: {
                          lineWidth: 1
                        }
                      },
                      threshold: null
                    }
                  },
                  series: [{
                    name: baseAsset,
                    data: baseBalanceHistory.map(cur => [new Date(cur.timestamp).getTime(), cur.value]),
                    // step: true,
                    // tooltip: {
                    //   valueDecimals: 6
                    // },
                  }]  
                }}
                // config={{

                // }}
              />
            </div>
          :
            <div className={`${ROOT_CLASS}__infoblock`}>
              <div className={`${ROOT_CLASS}__infoblocktitle`}>Income:</div>
              <div className={`${ROOT_CLASS}__infoblockcontent`}>No data yet</div>
            </div>  
        }
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Name product:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{name}</div>
        </div>
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Description product:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>{description}</div>
        </div>
        <div className={`${ROOT_CLASS}__infoblock`}>
          <div className={`${ROOT_CLASS}__infoblocktitle`}>Exchange:</div>
          <div className={`${ROOT_CLASS}__infoblockcontent`}>Binance</div>
        </div>
        {
          (balances.length > 0) ?
            <div className={`${ROOT_CLASS}__ratingvars`}>
              <div className={`${ROOT_CLASS}__ratingvars__left`}>
                <Highcharts 
                  config={{
                    chart: {
                      height: 200,
                      plotBackgroundColor: null,
                      plotBorderWidth: null,
                      plotShadow: false,
       
                      backgroundColor: 'rgba(0,0,0,0)',
                      type: 'pie',
                    },
                    title: {
                      text: null
                    },
                    subtitle: {
                      text: null
                    },
                    plotOptions: {
                      series: {
                        dataLabels: {
                          enabled: true,
                          format: '{point.name}: {point.y:.1f}'
                        }
                      }
                    },
                    credits: {
                      enabled: false
                    },
                    tooltip: {
                      headerFormat: '<span style="font-size:11px">{series.name}</span><br>',
                      pointFormat: '<span style="color:{point.color}">{point.name}</span>: <b>{point.y:.2f}</b> of total<br/>'
                    }, "series": [{
                      "name": "Assets",
                      "colorByPoint": true,
                      "data": balances.map((cur, key) => ({ 
                        name: cur.symbol,
                        y: cur.available,
                      }))
                    }], 
                  }}
                />
              </div>
              <div className={`${ROOT_CLASS}__ratingvars__right`}>
                <div className={`${ROOT_CLASS}__infoblock`}>
                  <div className={`${ROOT_CLASS}__infoblocktitle`}>Balances:</div>
                  <div className={`${ROOT_CLASS}__infoblockcontent balances`}>
                    {
                      balances.map((cur, key) => 
                        <div className={`${ROOT_CLASS}__balances`} key={key}>
                          <div className={`${ROOT_CLASS}__balance_item`}>
                            { cur.symbol } : { cur.available }
                          </div>
                        </div>
                      )
                    }
                  </div>
                </div>
              </div>
            </div>  
          :
            <div className={`${ROOT_CLASS}__infoblock`}>
              <div className={`${ROOT_CLASS}__infoblocktitle`}>Balances:</div>
              <div className={`${ROOT_CLASS}__infoblockcontent`}>No data yet</div>
            </div>  
        }
        {/*<div className={`${ROOT_CLASS}__chart`}>
          <StockAreaChart
            width={681}
            height={384}
            type="hybrid"
            zoom={false}
            data={props.history}
            stockProps={{
              stroke: "#494a4e",
              tickStroke: "rgba(255, 255, 255, 0.2)"
            }}
            axes
          />
        </div>*/}
        <div className={`${ROOT_CLASS}__footer-block`}>
          {
            (userStocks.length > 0) && 
              userStocks.map((cur, key) => {
                if (!cur.valid) return;

                let thisFollowing = myFollowings.some(cur2 => (
                  cur2.productId == productId &&
                  cur2.selfKeyId == cur.keyId
                ))

                if (thisFollowing) {
                  return (
                    <button
                      key={key}
                      className={`${ROOT_CLASS}__footer-button stop`}
                      onClick={props.onClickDisconnect.bind(null, productId, cur.keyId, cur.stock)}
                    >
                      Stop following for {cur.stock} key - {cur.keyId}
                    </button>
                  )
                } else {
                  return (
                    <button
                      key={key}
                      className={`${ROOT_CLASS}__footer-button`}
                      onClick={props.onClickConnect.bind(null, productId, cur.keyId, cur.stock)}
                    >
                      Start following for {cur.stock} key - {cur.keyId}
                    </button>
                  )
                }
              })
          }
        </div>
      </div>
    )
  }
}



