import {createStore, applyMiddleware, compose} from 'redux';
import {routerMiddleware} from 'react-router-redux';
import axios from 'axios';
import axiosMiddleware from 'redux-axios-middleware';
import thunk from 'redux-thunk';
import createHistory from 'history/createBrowserHistory';
import rootReducer from './modules';
// import { socketMiddleware } from './middlewares';
// import io from 'socket.io-client';
import { LocalStorage } from '@common/Utils';
import { networkError, baseURL } from './modules/common';
import { 
  refreshToken as refreshTokenRequest, 
  saveTokens,
  getTokens,
  removeTokens
} from '@store/modules/registration';

export const history = createHistory();

const client = axios.create({
  baseURL,
  responseType: 'json',
  headers: {
    'Content-Type': 'application/json',
    'Access-Control-Allow-Origin': '*'
  }
});

client.interceptors.request.use(function (config) {
  const tokens = getTokens();

  config.headers['Access-Token'] =  tokens.accessToken;
  config.headers['Refresh-Token'] =  tokens.refreshToken;

  return config;
});

// axios.defaults.headers.common['Authorization'] = store.getState().session.token;

// const socket = io('http://37.139.18.201');
// socket.on('disconnect', () => {
//   console.log('DISCONNECT');
// })

const axiosMiddlewareConfig = {
  onError: (info) => {
    const errorInfo = {
      error: info.error,
      meta: {
        previousAction: info.action
      },
      type: `${info.action.type}_FAIL`
    };
    errorInfo.error.message === 'Network Error' ?
      info.dispatch(networkError()) :
      info.dispatch(errorInfo);
    return Promise.reject(errorInfo);
  }
};

const checkJWT = store => next => action => {
  const payload = action.payload;
  const request = payload && payload.request ? payload.request : null;

  const axiosMiddleReturn = (body) => {
    return axiosMiddleware(client, axiosMiddlewareConfig)(store)(next)(body || action);
  }

  if (request && (action.jwt === undefined || action.jwt === true)) {
    const tokens = getTokens();

    if (!tokens.accessToken || !tokens.refreshToken) {
      // global.location.href = "/registration/sign-in";
      return axiosMiddleReturn();
    }
    
    return axiosMiddleReturn();
  } else if (action.jwt === false) {

   return axiosMiddleReturn();
  }

  return next(action);
};

const initialState = {};
const enhancers = [];
const middleware = [
  thunk,
  routerMiddleware(history),
  checkJWT,
  //axiosMiddleware(client, axiosMiddlewareConfig),
  //socketMiddleware(socket)
];

if (process.env.NODE_ENV === 'development') {
  const devToolsExtension = window.__REDUX_DEVTOOLS_EXTENSION__;

  if (typeof devToolsExtension === 'function') {
    enhancers.push(devToolsExtension());
  }
}

const composedEnhancers = compose(applyMiddleware(...middleware), ...enhancers);

export const store = createStore(rootReducer, initialState, composedEnhancers);



